//grid
$container-width: 83.5%;
$aside-width: 16.5%;
//Colors
$main-color: #393d45;
$text-color: #55687c;
$background-color: #3a5d82;
$white-color: #ffffff;
$info-background: #28415b;
$wrapper-color: #f9fcff;
$menu-hover-color: #e5ebf2;
//btn colors
$btn-color: #4f99ef;
$btn-color-enabled: #3b7ac2;
$btn-color-disabled: #a2c6f0;
//input colors
$success-color: #f24841;
$error-color: #f24841;
$input-background: #eaf1f9;
$input-background-hover: #d9e4f0;
$input-border: 1px solid #eaf1f9;
$input-border-focused: 1px solid #c5cfda;
$placeholder-color: #a5b5c5;
$hr-color: #c5cfda;

//input styles
$input-padding: 9px 15px;
$search-width: 200px;
$btn-width: 104px;
//language
$language-dark: #1d2e41;
$language-padding: 7px 23px 7px 8px;
$language-minwidth: 58px;
//Fonts
$main-font: 'Ubuntu-R';
$bold-font: 'Ubuntu-B';
$medium-font: 'Ubuntu-M';
//$safari-font: 'Ubuntu-R';
//Font-size and lineHeight
$h1-size: 24px;
$h1-height: 36px;
$menu-size: 14px;
$menu-height: 24px;
$main-size: 14px;
$main-Height: 24px;
$breadcrumbs-size: 12px;
$breadcrumbs-height: 18px;
$search-height: 36px;
$header-height: 50px;
//borders and shadows
$input-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
$input-radius: 5px;
$search-radius: 18.5px;
//content
$content-padding: 0px 20px;
$i: 10;

// single variables T_T
$th-even-color: #7095be;
$th-odd-color: #3a5d82;
$th-odd-active-color: #1cbbb4;
$th-even-active-color: #1b96a5;
$tbody-odd-color1: #f3f7fc;
$tbody-odd-color2: #e1ebf6;
$tbody-even-color1: #ffffff;
$tbody-even-color2: #eaf1f9;
$tbodylayer: rgba(142, 180, 224, 0.10);
$menu-padding: 16px 15px;
$additional-menu-padding: 7px 10px;
$news-background: #f2dad9;
$news-background-border: #efbfbd;
$aside-background: #eff3f8;
$max-width-text: 600px;
//$big-redbtn-color: #f24841;
$max-width-input-nomargin: 500px;
$max-width-input: 530px;
$min-button-width: 180px;
@while $i < 101 {
  .mb-#{$i} {
    margin-bottom: $i + px;
  }
  $i: $i + 10;
}

$i: 10;
@while $i < 101 {
  .mt-#{$i} {
    margin-top: $i + px;
  }
  $i: $i + 10;
}

$input-padding-vert: 11px 0px;