@import "index/index";

@media (max-width: 1200px) {
    @import "index/index-max-1200";
}

@media (max-width: 991px) {
    @import "index/index-max-992";
}

@media (max-width: 767px) {
    @import "index/index-max-767";
}
@media (max-width: 500px) {
    @import "index/index-max-500";
}
@media print{

    .codes {
        background-color: $menu-hover-color;
        margin-bottom: 20px;
        &__head {
            color: $text-color;
            background-color: $input-background;
            padding: 17px 0px;
            text-align: center;
            font-family: $bold-font;
        }
        &__tabs {
            font-size: $main-size;
            color: $text-color;
            font-size: 0px;
        }
        display: block;
        width: 100%;
        font-size: $main-size;
        color: $text-color;
        &__link {
            font-size: $main-size;
            padding: 15px 15px;
            color: $text-color;
        }
        &__show {
            padding: 0px 15px;
        }
        li {
            &.active {
                a {
                    background-color: $btn-color;
                    color: $white-color;
                }
            }
        }
        * {
            display: inline-block;
            vertical-align: middle;
        }
        &__item {
            display: inline-block;
            width: 32%;
            min-height: 400px;
            vertical-align: top;
            border: 1px dashed $hr-color;
            //padding: 20px 15px;
            margin-bottom: 4px;
            padding-bottom: 15px;
            font-family: $main-font;
            margin-right: 4px;
            position: relative;
            &:nth-child(3n) {
                //margin-right: 0px;
            }
            &:hover .codes__cross {
                display: block;
            }
        }
        &__cross {
            position: absolute;
            right: 10px;
            cursor: pointer;
            top: 10px;
            display: none;
        }
        &__text {
            .invitation__code {
                font-size: $main-size;
            }
            font-size: $main-size;
            color: $text-color;
            font-family: $main-font;
            margin: 15px 15px 0px 15px;
            &-light {
                margin: 15px 15px 0px 15px;
                font-size: $breadcrumbs-size;
                //line-height: $breadcrumbs-height;
                color: $placeholder-color;
            }
        }
    }
    //display none
    header{
        display: none;
    }
    aside{
        display:none;
    }


}