  &__theme {
    -webkit-box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
    -ms-box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
    -o-box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
