      margin-right: 0px;
    }
    &.active {
      .workingday__table {
        @include box-shadow($input-shadow);
      }
      .workingday__thead {
        //opacity: 0.2;
        & > tr {
          & > th {
            &:first-child {
              @include border-top-left-radius($input-radius);
              //@include border-top-right-radius($input-radius);
            }
            &:last-child {
              //@include border-top-left-radius($input-radius);
              @include border-top-right-radius($input-radius);
            }
            &:nth-child(odd) {
              background-color: $th-even-active-color;
            }
            background-color: $th-odd-active-color;
          }
        }
      }
    }
  }
  &__item.active {

  }
  &__comment {
    position: relative;
    &-text {
      white-space: normal;
      -webkit-box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
      -ms-box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
      -o-box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
      box-shadow: 0px 2px 15px 0px rgba(36, 74, 119, 0.4);
      background-color: #eaf1f9;
      padding: 16px 15px;
      -webkit-border-radius: 5px;
      border-radius: 5px;
      position: absolute;
      right: 30px;
      top: 0px;
      z-index: 9;
      width: 230px;
      text-align: left;
      display: none;
      &:after {
        position: absolute;
        right: -30px;
        top: 0px;
        display: block;
        height: 30px;
        //background-color: red;
        width: 30px;
        content: '';

      }
    }
  }
  &__theme {
    display: none;
  }
  &__first {
    width: 144px;
  }

  &__second {
    width: 300px;
    background-color: $tbodylayer;
  }
  &__third {
    width: 112px;
  }
  &__thead {
    th {
      padding: 10px 0px;
      height: 44px;
    }
    text-align: center;
    span {
      color: $white-color;
      line-height: 12px;
    }
    & > tr {

      & > th {
        &:first-child {
          @include border-top-left-radius($input-radius);
          //@include border-top-right-radius($input-radius);
        }
        &:last-child {
          //@include border-top-left-radius($input-radius);
          @include border-top-right-radius($input-radius);
        }
        &:nth-child(odd) {
          background-color: $th-even-color;
        }
        background-color: $th-odd-color;
      }
    }
  }
  &__tbody {

    td {
      padding: 9px 10px;

    }
    & > tr {
      &:nth-child(even) {
        background-color: $input-background;
        & > td {
          &:nth-child(even) {
            background-color: $tbody-even-color2;
          }
          &:nth-child(odd) {
            background-color: $tbody-odd-color2;
          }
        }
      }
      &:last-child {
        & > td {
          &:last-child {
            @include border-bottom-right-radius($input-radius);
          }
          &:first-child {
            @include border-bottom-left-radius($input-radius);
          }
        }
      }
      & > td {
        vertical-align: middle;
        &:nth-child(even) {
          background-color: $tbody-even-color1;
        }
        &:nth-child(odd) {
          background-color: $tbody-odd-color1;
        }
      }
    }
  }
}

